import React, { useState, useEffect } from 'react';
import Switch from 'react-switch';
import { Howl } from 'howler';
import './App.css';

const sounds = [
  { id: '1', title: 'Waterfall', file: '../assets/waterfall.mp3', image: '../assets/waterfall.jpg' },
  { id: '2', title: 'River', file: '../assets/river.mp3', image: '../assets/river.jpg' },
  { id: '3', title: 'Forest', file: '../assets/forest.mp3', image: '../assets/forest.jpg' },
  { id: '4', title: 'Fan', file: '../assets/fan.mp3', image: '../assets/fan.jpg' },
];

const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

const App = () => {
  const [playingSound, setPlayingSound] = useState(null);
  const [isDarkMode, setIsDarkMode] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    const shuffledSounds = [...sounds];
    const randomIndex = getRandomInt(2, shuffledSounds.length - 1);
    shuffledSounds.splice(randomIndex, 0, { id: 'ad-random', ad: true });
    setData([...shuffledSounds, { id: 'ad-bottom', ad: true }]);
  }, []);

  const playSound = (item) => {
    if (playingSound?.id === item.id) {
      playingSound.sound.stop();
      setPlayingSound(null);
      return;
    }

    if (playingSound) {
      playingSound.sound.stop();
    }

    const sound = new Howl({ src: [item.file], loop: true });
    sound.play();
    setPlayingSound({ id: item.id, sound });
  };

  const renderItem = (item) => {
    if (item.ad) {
      return (
        <div key={item.id} className={`item ad-container ${isDarkMode ? 'dark' : 'light'}`}>
          <p>Ad Placeholder</p>
        </div>
      );
    }

    const isPlaying = playingSound?.id === item.id;

    return (
      <div
        key={item.id}
        className={`item ${isDarkMode ? 'dark' : 'light'} ${isPlaying ? 'playing' : ''}`}
        onClick={() => playSound(item)}
      >
        <img src={item.image} alt={item.title} className="item-image" />
        <p>{item.title}</p>
      </div>
    );
  };

  return (
    <div className={`app ${isDarkMode ? 'dark' : 'light'}`}>
      <h1>Sleep Noise Maker</h1>
      <div className="toggle-container">
        <p>Dark Mode</p>
        <Switch
          checked={isDarkMode}
          onChange={() => setIsDarkMode(!isDarkMode)}
        />
      </div>
      <div className="grid">
        {data.map(renderItem)}
      </div>
      <div className={`ad-container-bottom ${isDarkMode ? 'dark' : 'light'}`}>
        <p>Ad Placeholder Bottom</p>
      </div>
    </div>
  );
};

export default App;
